import Vue from 'vue'
import App from './App.vue'
import router from './router'
import axios from 'axios'
import common from './components/common/copied.js'
import './style/dashboard.default.less'

import './remote/api.js';
import './style/index.scss';

Vue.config.productionTip = false

if (!Affinity) var Affinity = {};

if (!Affinity.dashboard) Affinity.dashboard = {};
Affinity.dashboard.config = {};

// Vue.component("dashboard", Dashboard);
Vue.prototype.$widgets = []
Vue.prototype.$apps = {}
// Vue.prototype.$config = null;
// Vue.prototype.$user = null;
// Vue.prototype.$changedConfig = false;

const config = Vue.observable({ config: null })

Object.defineProperty(Vue.prototype, '$config', {
    get () {
        return config.config
    },

    set (value) {
        config.config = value
    }
})

const user = Vue.observable({ user: null })

Object.defineProperty(Vue.prototype, '$user', {
    get () {
        return user.user
    },

    set (value) {
        user.user = value
    }
})

window.myVue = new Vue({
    router,
    render: h => h(App)
}).$mount('#app')
