<template>
    <div id="app">
<!--        <dashboard config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout"-->
<!--                   @ready="onDashboardReady">-->
        <div class="dashboard">
            <div class="pagewrapper">
                <div class="pagecontent">
                    <div class="contentwrapper">
                        <template v-if="ready">
                            <router-view></router-view>

                        </template>
                    </div>
                </div>
            </div>
        </div>


<!--        </dashboard>-->
    </div>
</template>

<script>
export default {
    data() {
        return {
            ready: false,
            localConfig: false
        }
    },
    watch: {
        $config: {
            handler(val) {
                console.log('config val', val)
                this.onDashboardReady(val)
            },
        }
    },
    methods: {
        onDashboardReady(config) {
            this.ready = true;
            // this.$widgets = [];
            // this.$apps = {};
            config.Apps.forEach((app) => {
                if (app.Widgets) {
                    app.Widgets.forEach((w) => {
                        if (w.Enabled) {
                            if (this.$user.EmployeeNo >= 5000000 && w.Name != 'Inbox') {
                                return;
                            }
                            this.$widgets.push(w.Name);
                        }
                    });
                }
                this.$apps[app.Name] = app;
            });
        }
    }
}
</script>

<style>
#app {
    height: 100%;
}
</style>